<template>
	<view class="month-payment-bill">
		
		<view class="current-month-bill">
			<view class="bill-top">
				<view class="bill-top-item">
					<view class="title">
						<text>当期账单</text>
					</view>
					<view class="amount current">
						<text>¥ 10000.00</text>
					</view>
				</view>
				
				<view class="bill-top-item">
					<view class="title">
						<text>未出账单</text>
					</view>
					<view class="amount not-already">
						<text>¥ 10000.00</text>
					</view>
				</view>
			</view>
			<view class="bill-bottom">
				<view class="title">
					<text>当月账单</text>
					<text class="title-desc">（6月10日出账）</text>
				</view>
				<view class="amount current-month">
					<text>¥ 10000.00</text>
				</view>
			</view>
		</view>
		
		<!-- 账单列表 -->
		<view class="month-bills-list">
			<view v-for="(item,index) in table.list" :key="index" class="bill-item" @click="onBillItemClick(index)">
