					<view class="bill-info-title">{{ item.title }}</view>
					<view class="bill-info-date">{{ item.startTime }}-{{ item.endTime }}</view>
				</view>
				<view class="bill-amount">
					<text>账单：¥3600.00</text>
				</view>
				<image class="bill-icon" src="/static/img/icon_go@3x.png" mode="widthFix"></image>
			</view>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				table: {
					list: [
						{
							title: '5月账单',
							startTime: '04/07',
							endTime: '04/07',
							amount: '3600',
						}
					]
				}
			}
		},
		methods: {
			onBillItemClick(index) {
				uni.navigateTo({
					url: '/pages/month-payment-order-detail/index'
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.month-payment-bill {
		min-height: 100%;
		background-color: #F0F2F5;
		padding: 30upx;
		box-sizing: border-box;
	}
	
	.current-month-bill {
		background-color: #fff;
		border-radius: 16upx;
		padding: 20upx;
		
		.title {
			font-size: 28upx;
			font-weight: 500;
			.title-desc {
				color: #A5A8BC;
			}
		}
		
		.amount {
			margin-top: 20upx;
			font-weight: 500;
			font-size: 32upx;
			
			&.current {
				color: $theme-color;
			}
			
			&.not-already {
				color: #A5A8BC;
			}
			
			&.current-month {
				color: #333333;
			}
		}
	}
	
	// 账单上部分
	.bill-top {
		@include flex;
		border-bottom: 2upx solid $border-color;
		padding-bottom: 30upx;
	}
	
	.bill-top-item {
		flex-grow: 1;
		flex-shrink: 0;
		flex-basis: 10upx;
		
		& + .bill-top-item {
			position: relative;
			margin-left: 30upx;
			
			&::after {
				content: '';
				position: absolute;
				top: 0;
				bottom: 0;
				left: -30upx;
				border-left: 2upx solid $border-color;
			}
		}
	}
	
	// 账单下部分
	.bill-bottom {
		padding-top: 30upx;
	}
	
	.month-bills-list {
		margin-top: 30upx;
	}
	
	.bill-item {
