	}
	
	.bill-info-title {
		font-size: 34upx;
		font-weight: 500;
	}
	
	.bill-info-date {
		margin-top: 18upx;
		font-size: 28upx;
		color: #A5A8BC;
	}
	
	.bill-amount {
		flex-shrink: 0;
		color: #999999;
		font-size: 32upx;
	}
	
	.bill-icon {
		width: 36upx;
		height: 36upx;
		flex-shrink: 0;
		margin-left: 14upx;
	}
	
	
	
